/* src/components/HomePage.css */
.home-page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.section {
    padding: 80px 20px;
}

.content-container {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    gap: 50px;
}

.text-content,
.image-content {
    flex: 1;
}

.text-content h2 {
    font-size: 2.5rem;
    color: #2c3e50;
    margin-bottom: 20px;
}

.text-content p {
    font-size: 1.1rem;
    color: #555;
    margin-bottom: 15px;
    line-height: 1.8;
}

.business-image {
    height: 350px;
    background-color: #e0e0e0;
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    border: 1px solid #ccc;
}

/* 轮播图样式 */
.hero-slider {
    position: relative;
    height: 80vh;
    overflow: hidden;
}

.slider-container {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
}

.slider-content {
    text-align: center;
    max-width: 800px;
    padding: 20px;
}

.slider-content h1 {
    font-size: 3.5rem;
    margin-bottom: 20px;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.slider-content p {
    font-size: 1.5rem;
    margin-bottom: 30px;
    text-shadow: 0 1px 3px rgba(0, 0, 0, 0.3);
}

.cta-button {
    background-color: #ff7e5f;
    color: white;
    border: none;
    padding: 12px 30px;
    font-size: 1.2rem;
    border-radius: 30px;
    cursor: pointer;
    transition: all 0.3s ease;
    font-weight: bold;
}

.cta-button:hover {
    background-color: #feb47b;
    transform: translateY(-3px);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.slider-indicators {
    position: absolute;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    gap: 10px;
}

.indicator {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.5);
    border: none;
    cursor: pointer;
    transition: all 0.3s ease;
}

.indicator.active {
    background-color: white;
    transform: scale(1.2);
}

/* 市场分析样式 */
.market-analysis {
    background-color: #f9f9f9;
}

.market-analysis h2 {
    text-align: center;
    margin-bottom: 50px;
}

.stats-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 30px;
    max-width: 1200px;
    margin: 0 auto;
}

.stat-card {
    background: white;
    padding: 30px;
    border-radius: 10px;
    text-align: center;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
    transition: transform 0.3s ease;
}

.stat-card:hover {
    transform: translateY(-10px);
}

.stat-value {
    font-size: 3rem;
    font-weight: bold;
    color: #4b6cb7;
    margin-bottom: 10px;
}

.stat-label {
    font-size: 1.1rem;
    color: #666;
}

/* 产品与服务样式 */
.products-services h2 {
    text-align: center;
    margin-bottom: 50px;
}

.services-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 30px;
    max-width: 1200px;
    margin: 0 auto;
}

.service-card {
    text-align: center;
    padding: 30px 20px;
    border-radius: 10px;
    background: white;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
    transition: all 0.3s ease;
}

.service-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
}

.icon {
    width: 80px;
    height: 80px;
    margin: 0 auto 20px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    color: white;
}

.health {
    background: linear-gradient(135deg, #4b6cb7 0%, #182848 100%);
}

.social {
    background: linear-gradient(135deg, #ff7e5f 0%, #feb47b 100%);
}

.smart-home {
    background: linear-gradient(135deg, #56ab2f 0%, #a8e063 100%);
}

.training {
    background: linear-gradient(135deg, #8E2DE2 0%, #4A00E0 100%);
}

.service-card h3 {
    font-size: 1.5rem;
    margin-bottom: 15px;
    color: #2c3e50;
}

.service-card p {
    color: #666;
    font-size: 1rem;
}

/* 产品展示区 */
.product-showcase {
    background-color: #f0f7ff;
}

.product-showcase h2 {
    text-align: center;
    margin-bottom: 50px;
}

.showcase-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 40px;
    max-width: 1200px;
    margin: 0 auto;
}

.product-card {
    background: white;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
}

.product-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.15);
}

.product-image {
    height: 250px;
}

.fall-detection {
    background: linear-gradient(to right, #e0f0ff, #c2e0ff);
}

.monitoring {
    background: linear-gradient(to right, #ffe0e0, #ffc2c2);
}

.bracelet {
    background: linear-gradient(to right, #e0ffe0, #c2ffc2);
}

.product-card h3 {
    font-size: 1.5rem;
    padding: 20px 20px 10px;
    color: #2c3e50;
}

.product-card p {
    padding: 0 20px 20px;
    color: #666;
}

/* 营销策略样式 */
.marketing-strategy {
    background-color: #f9f9f9;
}

.image-content {
    height: 366px;
    overflow: hidden;
    border-radius: 5px;
}

.marketing-strategy ul {
    list-style-type: none;
    padding: 0;
}

.marketing-strategy li {
    padding: 15px 0;
    font-size: 1.1rem;
    position: relative;
    padding-left: 30px;
}

.marketing-strategy li:before {
    content: "✓";
    position: absolute;
    left: 0;
    color: #4b6cb7;
    font-weight: bold;
}

.strategy-img {
    width: 100%;
    height: auto;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

/* 财务计划样式 */
.financial-plan h2 {
    text-align: center;
    margin-bottom: 50px;
}

.plan-container {
    display: flex;
    justify-content: center;
    gap: 50px;
    max-width: 800px;
    margin: 0 auto;
}

.plan-card {
    text-align: center;
    padding: 30px;
    border-radius: 10px;
    background: white;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
    flex: 1;
    transition: all 0.3s ease;
}

.plan-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
}

.amount {
    font-size: 2.5rem;
    font-weight: bold;
    color: #4b6cb7;
    margin: 20px 0;
}

.plan-card p {
    color: #666;
}